    BeagleText1:
      "Beagles were developed in England as hunting dogs to chase down small prey such as rabbits. A lot has changed since the origin and perfection of the breed. Today, many people still use Beagle to hunt rabbit... but just as many keep this dog breed as a family companion pet. Males are almost always larger than females. This makes breeding a bit tricky, as the best pairing is a larger female and a smaller male dog",
    BeagleText2:
      " While usually classified as a “low shedder“ due to the very short coat...all dogs shed. It is a normal process for each hair follicle to fall out after it grows to a certain length and then is replaced by another. However, it is true that some dogs shed much lighter than other.",
    BeagleText3:
      "When it comes to knowing this about Beagles, this breed is a light shedder. There are times when the shed may be heavier. This will happen to a more moderate degree if the dog lives in an area that experiences extreme seasonal temperature changes. Also, females may “	blow the coat“ after a heat cycle due to hormonal changes and many females lose some coat after giving birth. ",
    BeagleText4:
      "All Beagles have white tipped tails, although it may not be apparent. Some have very few white hairs, only noticeable upon very close inspection, others (usually hunter Beagles) will have hair loss on the tip and there are some that have docked tails although this goes against the AKC breed standard.",
    BeagleText5:
      "However, this is a strong trait that has been bred into the Beagle bloodline. While extremely friendly and an absolutely wonderful family pet, owners should be careful when a Beagle is around other small pets such as chinchillas, hamsters, mice, etc.",
    BeagleText6:
      "BODY: Short in the couplings, but well balanced. Topline: Straight and level. Loin: Loins powerful and supple. Chest: Let down to below elbow. Ribs well sprung and extending well back. Underline and belly: Without excessive tuck-up. ",
    BeagleText7:
      "GAIT / MOVEMENT: Back level, firm with no indication of roll. Stride free, long reaching in front and straight without high action; hind legs showing drive. Should not move close behind nor paddle nor plait in front. ",
