    BeagleText1:
      "Бигли появились еще в средневековой Англии. Королевская знать использовала их для выслеживания оленей и косуль и охоты на зайцев. История происхождения биглей до сих пор до конца не изучена: если верить историкам, прародители этих милых собачек появились еще в древнем Риме и уже оттуда были завезены в Англию в 1066 году во время Нормандского похода, где на протяжении нескольких столетий скрещивались с гончими породами",
    BeagleText2:
      "Особую популярность бигли получили в период правления Эдуарда II и Генриха VII, а королева Елизавета I даже содержала целый питомник «карманных» биглей размером с перчатку. В 18 веке разведением породы занялись английские ирландские фермеры, которые охотились вместе с ними на кроликов и зайцев. А в 19 веке в Эссексе основательно принялись за выведение охотничьих собак, которые и стали прародителями современных биглей.",
    BeagleText3:
      "ОБЩИЙ ВИД: Крепкая гончая компактного сложения, производящая впечатление некоторой грубости.",
    BeagleText4:
      "ПОВЕДЕНИЕ И ТЕМПЕРАМЕНТ: Задорная гончая, чья основная функция – охота, главным образом на зайца, с помощью нижнего чутья. Смелая, с огромной энергией, выносливостью и решительностью. Бдительная, смышлёная и уравновешенная. Дружелюбная и внимательная, не проявляющая агрессии или трусости. ГОЛОВА: Средней длины, массивная без грубости, изящнее у сук, лишена нахмуренности и морщинистости. ЧЕРЕПНАЯ КОРОБКА: Череп слегка выпуклый, умеренно широкий, с лёгким гребнем. Переход от лба к морде: Хорошо выражен, находится по возможности на одинаковом расстоянии как от затылочного бугра, так и от кончика мочки носа.",
    BeagleText5:
      "ЛИЦЕВАЯ ЧАСТЬ: Мочка носа: Крупная, предпочтительно чёрная, но ослабленная пигментация допустима у светлоокрашенных собак. Ноздри хорошо раскрыты.",
    BeagleText6:
      "Морда - Нелёгкая. Губы - В меру брылястые. Челюсти и зубы: Челюсти должны быть крепкими, с совершенным, правильным и полностью ножницеобразным прикусом, т.е. верхние резцы плотно перекрывают нижние и стоят перпендикулярно челюстям.",
    BeagleText7:
      "ГЛАЗА: Тёмно-коричневые или цвета лесного ореха, в меру крупные, не углублённые и не выпуклые, широко посажены, с кротким, просительным взглядом.",
    BeagleText8:
      "УШИ: Длинные, с округлыми концами, почти доходящие до кончика мочки носа при вытягивании. Низко посажены, тонкие, висячие, элегантно примыкают к скулам.",
